import BatuhanFoto from "../assets/BatuhanFoto (1).jpg";
import { useSelector } from "react-redux";

export default function MainContent() {

    const { theme } = useSelector(state => state.theme)

    if(!localStorage.getItem("Foto-MainPage")){
        localStorage.setItem("Foto-MainPage",BatuhanFoto)
    }

    return (
        <div className={theme == "dark" ? "mainDark" : "main"}>
            <div className="maincontent">
                <img src={localStorage.getItem("Foto-MainPage")} className="batuFoto"></img>

                <div className="mainContent-text">
                    <p className={theme == "dark" ? "textDark" : "text"}>
                        Merhaba, ben Batuhan. 16 yandaym, Nevzat Ayaz Anadolu Lisesinde 10.snf rencisiyim. Yazlm sektrne ilk olarak
                        2020 ylnda adm attm. Yazlma C# renerek baladm daha sonrasnda Cpp ve Java dillerinde ksa bir tecrbem oldu. Yaklak bir yl nce
                        Html renerek web tarafna ilk admm atm oldum. Kendimi bu dilde gelitirmekle beraber CSS ve Javascripte de renerek kariyerimi hzl 
                        bir ekilde gelitiriyorum.
                    </p>
                </div>
            </div>
        </div>
    )
}