import profileFoto from "../assets/googleprofilfoto.jpg";
import { Link } from "react-router-dom";
import { setTheme } from "../stores/theme";
import { useSelector, useDispatch } from "react-redux";

export default function Header() {

    const dispatch = useDispatch()

    const { theme } = useSelector(state => state.theme);

    const TemaAyarla = () => {
        if (theme == "dark") {
            dispatch(setTheme("light"))
            console.log("Temann uan light olmas lazm")
        } else {
            dispatch(setTheme("dark"))
            console.log("Temann uan dark olmas lazm")
        }
    }

    return (
        <div className={theme == "dark" ? "headerDark" : "header"}>
            <img src={profileFoto} alt="resim bulunamad" />
            <ul>
                <li>
                    <Link to="/anasayfa" className="link">
                        Anasayfa
                    </Link>
                </li>
                <li>
                    <Link to="/okul" className="link">
                        Okul
                    </Link>
                </li>
            </ul>
            <button className="temaButton" onClick={TemaAyarla}>
                {theme == "dark" ? "Ak" : "Koyu"}
            </button>
        </div>
    )
}