import { signUp } from "../config/firebase"
import { useState, useEffect, useContext } from "react"
import { Link, useNavigate } from "react-router-dom";
import { setUser } from "../stores/auth";
import { useSelector, useDispatch } from "react-redux";
import { toast } from "react-hot-toast";

export default function SignUp() {

    console.log("signup a ynlendirme baarl")

    const [Name, setName] = useState();
    const [Email, setEmail] = useState();
    const [Password, setPassword] = useState();

    const navigate = useNavigate();

    const dispatch = useDispatch()

    const { user } = useSelector(state => state.auth);

    useEffect(() => {
        if (user) {
            console.log("signup mesaj")
            navigate("/anasayfa")
        }
    })

    const handleSubmit = async (e) => {

        try {
            e.preventDefault()

            await signUp(Name,Email, Password)
        } catch (err) {
            toast.error(err.message)
        }

        // e.preventDefault();

        // await signUp(Name, Email, Password)

        // .then(() => {
        //     console.log("Kayt baarl");
        //     dispatch(setUser(true))
        //     navigate("/anasayfa")
        // })
        // .catch((err) => {
        //     toast.error(err.code, err.message)
        // })
    }

    return (
        <>
            <form onSubmit={handleSubmit}>
                <input type="text" placeholder="sim" onChange={(e) => { setName(e.target.value) }} />
                <input type="email" placeholder="Email" onChange={(e) => { setEmail(e.target.value) }} />
                <input type="password" placeholder="ifre" onChange={(e) => { setPassword(e.target.value) }} />
                <button type="submit">Kayt ol</button>
            </form>
            <p>
                Hesabn var m? <Link to="/login">Giri yap</Link>
            </p>
        </>
    )
}